export default {
    name: "web-login",
    data() {
        return {
            token: '',
        }
    },
    onLoad(options) { this.$commonLoad.onload(options);
        this.token = options.token;
        if (!this.token) {
            uni.showModal({
                title: '提示',
                content: '无效的Token，请刷新页面后重新扫码登录',
                showCancel: false,
                success: function (e) {
                    if (e.confirm) {
                        uni.redirectTo({
                            url: '/pages/index/index',
                        });
                    }
                }
            });
        }
    },
    methods: {
        navIndex() {
            uni.redirectTo({url: `/pages/index/index`});
        },

        loginSubmit: function () {
            const self = this;
            self.$showLoading({text: '正在处理'});
            self.$request({
                url: self.$api.mch.qr_code_login,
                data: {
                    token: self.token,
                },
                method: 'post'
            }).then(info => {
                self.$hideLoading();
                uni.showModal({
                    title: '提示',
                    content: info.msg,
                    showCancel: false,
                    success: function (e) {
                        if (e.confirm) {
                            uni.redirectTo({url: '/pages/index/index'});
                        }
                    }
                });
            });
        },
    }
}
